import React, { useEffect, useState } from "react";
import BackgroundPurple from "../../components/Background/BackgroundPurple";
import JoinInput from "../../components/Input/JoinInput";
import JoinButton from "../../components/Button/JoinButton";
import Logo from "../../components/Logo/Logo";
import LoginModal from "./LoginModal";
import { setCookie, getCookie } from "../../cookie";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";

function Login() {
    const navigate = useNavigate()

    const [name, setName] = useState("");

    const nameChangeHandler = (e) => {
        setName(e.target.value)
    }
    const onSubmitHandler = (e) => {
        e.preventDefault()

        if (name == null || name === "") {
            Swal.fire({
                title: " !",
                confirmButtonText: "",
                confirmButtonColor: "#9E40D7",
            });
            return
        }
        
        const code = new Date().getTime();
        
        setCookie("name", name);
        setCookie("code", code);

        navigate("/page", {replace: true})
    }
    
    useEffect(() => {
        if (getCookie("name") && getCookie("code")) {
            navigate("/page", {replace: true})
        }
    }, [])

    return(
        <BackgroundPurple>
            <Logo isCenter={true}/>
            <LoginModal>
                <form onSubmit={onSubmitHandler}>
                    <JoinInput id="name" placeholder=" ." value={name} onChange={nameChangeHandler} />
                    <JoinButton></JoinButton>
                </form>
                <h3>Party Group</h3>
                <p>
                    <span>Party Group</span> <b>  </b> !<br />
                    <br />
                    , , ,   <br />
                    <span>Party Group</span>    !<br />
                    <br />
                         ?<br />
                    <b></b>   !<br />
                      10 12 !<br />
                </p>
            </LoginModal>
        </BackgroundPurple>
    )
}

export default Login